import currency from 'currency.js';

const formatMoney = (amount: number, currencyCode: string) => {
  // âœ… Set symbol to â‚¹ for INR, fallback to Â£ or $
  const symbol = currencyCode === "INR" ? "â‚¹" : currencyCode === "GBP" ? "Â£" : "$";

  return currency(amount, {
    symbol,
    precision: 0, // âœ… Indian currency is often shown without decimals for whole amounts
    separator: ",",
    decimal: ".",
    // âœ… This ensures the symbol appears before the number (e.g., â‚¹ 50,000)
    pattern: `! #`, 
  }).format();
};

export default formatMoney;